.wrapper {
    background: #FFFFFF;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    padding: 12px;
    transition: background-color 0.2s linear;
}
.wrapper:hover {
    cursor: pointer;
    background-color: #cacaca;
}

.wrapper.draggable {
    opacity: 0.5;
}